<template>
  <v-stepper :value="step">
    <v-stepper-header>
      <v-stepper-step step="1" :complete="step > 1">基本信息</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="2" :complete="step > 2">商品描述</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3" >商品图片</v-stepper-step>
    </v-stepper-header>
    <v-stepper-items>
      <!--基本信息的表单-->
      <v-stepper-content step="1">
        <v-layout justify-center>
          <v-flex xs10>
            <v-form v-model="valid" ref="basic">
              <v-layout row>
                <v-flex xs5>
                  <v-text-field label="商品分类" v-model="goods.ptype" required
                                :rules="[v => !!v || '商品分类不能为空']"></v-text-field>
                </v-flex>
                <v-flex offset-xs2 xs5>
                  <v-text-field label="商品标签" v-model="goods.plabel"  required
                                :rules="[v => !!v || '商品标签不能为空']"></v-text-field>
                </v-flex>
              </v-layout>

              <v-text-field label="商品名称" v-model="goods.pname"  required
                            :rules="[v => !!v || '商品标题不能为空']"></v-text-field>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field label="商品原价" v-model="goods.poldprice" required
                                :rules="[v => !!v || '商品原价不能为空']"
                                mask="######"></v-text-field>
                </v-flex>
                <v-flex offset-xs2 xs5>
                  <v-text-field label="商品现价" v-model="goods.pnewprice"  required
                                :rules="[v => !!v || '商品现价不能为空']"
                                mask="######"></v-text-field>
                </v-flex>
              </v-layout>
              <v-text-field label="商品星级" v-model="goods.pstarlevel" :rules="[v => (v>-1&&v<=5) || '商品星级不能为空且不能大于5']" mask="#"></v-text-field>
              <v-layout row>
                <v-flex xs3>
                </v-flex>
                <v-flex>
                </v-flex>
              </v-layout>
            </v-form>
          </v-flex>
        </v-layout>
      </v-stepper-content>
      <!--商品描述-->
      <v-stepper-content step="2">
        <v-editor v-model="goods.pinfo" upload-url="/Upload/image" />
      </v-stepper-content>
      <!--规格参数-->
      <v-stepper-content step="3">
        <v-layout row>
          <v-flex xs3>
            <span style="font-size: 16px; color: #444">商品图片：</span>
          </v-flex>
          <v-flex>
            <v-upload
              v-model="goods.productImgs" @input="listupload" url="/Upload/image" :multiple="true" :pic-width="250" :pic-height="90"
            />
          </v-flex>
        </v-layout>
      </v-stepper-content>

    </v-stepper-items>
  </v-stepper>
</template>

<script>
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'

  import {quillEditor} from 'vue-quill-editor'

  export default {
    name: "goods-form",
    components: {
      quillEditor
    },
    props: {
      step: {
        type: Number,
        default: 1,
      },
      isEdit: {
        type: Boolean,
        default: false
      },
      oldGoods: Object
    },
    data() {
      return {
        valid: false,
        headers: [],// 表头
        goods: {
          pid:'',
          pname:'',
          pstarlevel:'',
          ptype:'',
          pnewprice:'',
          poldprice:'',
          pinfo:'',
          plable:'',
          productImgs:[]
        },
      }
    },
    methods: {

      deleteOption(i, j) {
        this.skuTemplate[i].options.splice(j, 1);
      },
      listupload(list){
        //对回显的图片数据进行封装
       this.goods.productImgs=list;
       console.log(this.goods.productImgs);
      },
      submit() {
        this.goods.productImgs=this.goods.productImgs.map(f =>f.imgurl);
        this.goods.productImgs=this.goods.productImgs && this.goods.productImgs.length > 0 ? this.goods.productImgs.join(",") : "";
        if(this.goods.productImgs.length<=0)
        { this.$message.error("至少选择一张图片！");
        return;
        }
        console.log(this.goods.productImgs);
        this.$http.post(this.isEdit ? '/ProductManage/edit' : '/ProductManage/add',this.$qs.stringify(this.goods))
          .then(() => {
            this.$message.success("提交商品成功！");
            this.$emit("closeForm")
          })
          .catch((e) => {
            console.log(e)
            this.$message.error("提交商品失败！");
            this.$emit("closeForm")
          })
      }
    },
    watch: {
      oldGoods: {
        immediate: true,
        deep:true,
        handler(val) {
          if (val == null || !this.isEdit) {
            return;
          }
          this.goods=Object.deepCopy(val)
        }
      }

    }
  }
</script>

<style scoped>

</style>



