<template>
  <v-card>
    <v-toolbar class="elevation-0">
      <v-btn @click="addItem" color="primary">新增商品</v-btn>
      <v-spacer/>
      <v-flex xs3>
        状态：
        <v-btn-toggle mandatory v-model="search.saleable">
          <v-btn flat>
            全部
          </v-btn>
          <v-btn flat :value="true">
            上架
          </v-btn>
          <v-btn flat :value="false">
            下架
          </v-btn>
        </v-btn-toggle>
      </v-flex>
      <v-flex xs3>
        <v-text-field
          append-icon="search"
          label="搜索"
          single-line
          hide-details
          v-model="search.key"
        />
      </v-flex>
    </v-toolbar>
    <v-divider/>
    <v-data-table
      :headers="headers"
      :items="items"
      :total-items="totalItems"
      :loading="loading"
      class="elevation-1"
      item-key="pid"
      hide-actions
    >
      <template slot="items" slot-scope="props">
        <tr @click="props.expanded = !props.expanded">
        <td class="text-xs-center">{{ props.item.pid }}</td>
        <td class="text-xs-center">{{ props.item.pname }}</td>
        <td class="text-xs-center">{{ props.item.ptype}}</td>
        <td class="text-xs-center">${{ props.item.pnewprice }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon small @click.stop="editItem(props.item)">
            <i class="el-icon-edit"/>
          </v-btn>
          <v-btn icon small @click.stop="deleteItem(props.item.pid)">
            <i class="el-icon-delete"/>
          </v-btn>
        </td>
        </tr>
      </template>
      <template slot="no-data">
        <v-alert
          :value="true"
          color="info"
          icon="info"
          outline
        >
          数据正在加载中
        </v-alert>
      </template>
      <template slot="expand" slot-scope="props">
        <v-card flat>
          <v-card-text class="text-xs-left">{{props.item.pinfo}}</v-card-text>
        </v-card>
      </template>
    </v-data-table>

    <v-dialog v-if="show" v-model="show" max-width="900" scrollable persistent>
      <v-card>
        <v-toolbar dense dark color="primary">
          <v-toolbar-title>{{isEdit ? '修改商品' : '新增商品'}}</v-toolbar-title>
          <v-spacer/>
          <v-btn icon @click="closeForm">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text style="height: 600px;">
          <!-- 表单 -->
          <goods-form :oldGoods="selectedGoods" :isEdit="isEdit" :step="step" ref="goodsForm" @closeForm="closeForm"/>
        </v-card-text>
        <v-card-actions>
          <v-layout row justify-center>
            <v-flex xs2>
              <v-btn :disabled="step === 1" @click="lastStep">上一步</v-btn>
            </v-flex>
            <v-flex xs2>
              <v-btn v-if="step === 3" color="primary" @click="submit">完成</v-btn>
              <v-btn v-else color="primary" @click="nextStep">下一步</v-btn>
            </v-flex>
          </v-layout>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
  import GoodsForm from './GoodsForm'
  import config from '../../config'

  export default {
    name: "item",
    components: {
      GoodsForm
    },
    data() {
      return {
        imgurl:"http://47.100.52.248/cart/assets/images/products/single/product-shop-1-2.jpg",
        expand:false,
        search: {
          key: '',
          saleable: true,
        },// 过滤字段
        totalItems: 0,// 总条数
        items: [],// 表格数据
        loading: true,
        pagination: {},// 分页信息
        headers: [// 表头
          {text: 'pid', align: 'center', value: 'id',sortable: false},
          {text: '名称', align: 'center', sortable: false, value: 'name'},
          {text: '分类', align: 'center', sortable: false, value: 'image'},
          {text: '价格', align: 'center', value: 'letter', sortable: false,},
          {text: '操作', align: 'center', value: 'id', sortable: false}
        ],
        show: false,// 是否弹出窗口
        selectedGoods: null, // 选中的商品信息
        isEdit: false, // 判断是编辑还是新增
        step: 1// 表单中的导航条
      }
    },
    watch: {
      pagination: {
        handler() {
          this.getDataFromApi();
        },
        deep: true
      },
      search: {
        handler() {
          this.getDataFromApi();
        },
        deep: true
      }
    },
    mounted() {
      this.getDataFromApi();
    },
    methods: {
      closeForm() {
        this.isEdit = false;
        this.show = false;
        this.step = 1;
        this.selectedGoods = null;
        this.getDataFromApi();
      },
      lastStep() {
        if (this.step === 1) {
          return;
        }
        this.step--;
      },
      nextStep() {
        if (this.step === 4) {
          return;
        }
        if (this.$refs.goodsForm.$refs.basic.validate()) {
          this.step++;
        }
      },submit(){
        this.$refs.goodsForm.submit();
      },
      addItem() {
        this.selectedGoods = null;
        this.isEdit = false;
        this.show = true;
      },
      editItem(item) {
        this.selectedGoods = item;
        this.isEdit = true;
        this.show = true;
      },
      deleteItem(pid) {
        this.$message.confirm('此操作将永久删除该商品, 是否继续?')
          .then(() => {
            // 发起删除请求
            this.$http.delete("/ProductManage/delete?pid=" +pid)
              .then(() => {
                // 删除成功，重新加载数据
                this.getDataFromApi();
                this.$message.info('删除成功!');
              })
          })
          .catch(() => {
            this.$message.info('已取消删除');
          });

      },
      getDataFromApi() {
        this.loading = true;
        this.$http.get("/ProductManage/list")
          .then(resp => {
            this.items=resp.data;
            this.loading = false;
          }).catch(error =>{
          this.$message.info("获取数据失败！");
        })
      }
    }
  }
</script>

<style scoped>
  label {
    font-size: 14px;
  }
</style>



